* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flag-country-block {
  display: flex;
  align-items: center;
  width: 50%;
}

.flag {
  height: 20px;
  margin-right: 10px;
}
.country-info-details {
  padding-left: 0px;
  list-style-type: none;
}
.country-info-item {
  margin: 0;
}

h1 {
  display: inline-block;
  margin: 0;
}

h2 {
  display: inline-block;
  margin: 0;
}

.info-value {
  font-size: 1.4em;
  font-family: sans-serif;
}

.country-list {
  padding-left: 0px;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.country-list-item {
  display: flex;
  align-items: center;
}

.flag-list {
  margin-right: 10px;
  height: auto;
  width: 30px;
}

.list-item-h2 {
  display: inline-block;
  margin: 0;
  font-size: 1.5em;
  font-family: sans-serif;
}
